<template>
<div class="infoFooter">
  <div class="container">
    <!--  -->
    <div class="footer-service">
      <a :href="n.sourceUrl" class="item" target="_blank" v-for="n in serviceNav">
        <i class="fa icon" :class="'fa-'+n.type"></i> {{n.title}}
      </a>
    </div>
    <!--  -->
    <div class="footer-links">
      <dl class="col-links" v-for="(item,index) in linkNav" :key="index">
        <dt>{{item.title}}</dt>
        <dd v-for="n in item.list">
          <a :href="n.sourceUrl">{{n.title}}</a>
        </dd>
      </dl>
      <div class="col-contact">
        <h2 class="title">400-100-5678</h2>
        <p class="time"> 8:00-18:00</p>
        <p class="fee"></p>
        <a href="//www.mi.com/service/contact" target="_blank">
          <i class="fa fa-commenting"></i> 24
        </a>
      </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: "infoFooter",
  data: function data() {
    return {
      serviceNav: [{
          sourceUrl: '//www.mi.com/static/fast/',
          title: '',
          type: 'cog'
        },
        {
          sourceUrl: '//www.mi.com/service/exchange#back',
          title: '7',
          type: 'circle'
        },
        {
          sourceUrl: '//www.mi.com/service/exchange#free',
          title: '15',
          type: 'circle'
        },
        {
          sourceUrl: '//www.mi.com/service/exchange#mail',
          title: '150',
          type: 'gift'
        },
        {
          sourceUrl: '//www.mi.com/static/maintainlocation/',
          title: '520',
          type: 'map-marker'
        }
      ],
      linkNav: {
        help: {
          title: '',
          list: [{
              sourceUrl: '//www.mi.com/service/account/register/',
              title: ''
            },
            {
              sourceUrl: '//www.mi.com/service/buy/buytime/',
              title: ''
            },
            {
              sourceUrl: '//www.mi.com/service/order/sendprogress/',
              title: ''
            }
          ]
        },
        service: {
          title: '',
          list: [{
              sourceUrl: '//www.mi.com/service/exchange',
              title: ''
            },
            {
              sourceUrl: 'http://fuwu.mi.com/',
              title: ''
            },
            {
              sourceUrl: 'http://xiazai.mi.com/',
              title: ''
            }
          ]
        },
        merchants: {
          title: '',
          list: [{
              sourceUrl: 'href="//www.mi.com/c/xiaomizhijia/"',
              title: ''
            },
            {
              sourceUrl: '//www.mi.com/static/maintainlocation/',
              title: ''
            },
            {
              sourceUrl: '//www.mi.com/static/familyLocation/',
              title: ''
            }
          ]
        },
        about: {
          title: '',
          list: [{
              sourceUrl: '//www.mi.com/about',
              title: ''
            },
            {
              sourceUrl: 'http://hr.xiaomi.com/',
              title: ''
            },
            {
              sourceUrl: '//www.mi.com/about/contact',
              title: ''
            }
          ]
        },
        mi: {
          title: '',
          list: [{
              sourceUrl: 'http://weibo.com/xiaomishangcheng',
              title: ''
            },
            {
              sourceUrl: 'http://xiaoqu.qq.com/mobile/share/index.html?url=http%3A%2F%2Fxiaoqu.qq.com%2Fmobile%2Fbarindex.html%3Fwebview%3D1%26type%3D%26source%3Dindex%26_lv%3D25741%26sid%3D%26_wv%3D5123%26_bid%3D128%26%23bid%3D10525%26from%3Dwechat',
              title: ''
            },
            {
              sourceUrl: '#J_modalWeixin',
              title: ''
            }
          ]
        },
        feature: {
          title: '',
          list: [{
              sourceUrl: '//order.mi.com/queue/f2code',
              title: 'F '
            },
            {
              sourceUrl: '//www.mi.com/giftcode/',
              title: ''
            },
            {
              sourceUrl: '//order.mi.com/misc/checkitem',
              title: ''
            }
          ]
        }
      }
    }
  }
}
</script>
<style lang="less" scoped>
@baseColor: #ff6700;
.infoFooter {
    .container {
        width: 1226px;
        height: auto;
        margin: 0 auto;
        background: #fff;
        .footer-service {
            padding: 27px 0;
            border-bottom: 1px solid #e0e0e0;
            display: flex;
            justify-content: flex-start;
            flex-flow: row nowrap;
            align-items: center;
            .item {
                display: inline-block;
                width: 19.8%;
                height: 25px;
                border-right: 1px solid #e0e0e0;
                font-size: 16px;
                line-height: 25px;
                text-align: center;
                color: #616161;
                transition: all 0.3s ease;
                &:hover {
                    color: @baseColor;
                }
                &:last-child {
                    border: none;
                }
                .icon {
                    margin-right: 6px;
                    font-size: 24px;
                    line-height: 24px;
                    vertical-align: -4px;
                }
            }
        }
        .footer-links {
            padding: 40px 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-flow: row nowrap;
            .col-links {
                width: 160px;
                height: 112px;
                dt {
                    font-size: 14px;
                    color: #424242;
                    margin: 0 0 26px;
                }
                dd {
                    margin: 16px 0 0;
                    font-size: 12px;
                    a {
                        color: #757575;
                        &:hover {
                            color: @baseColor;
                        }
                    }
                }
            }
            // flexmargin-left
            .col-contact {
                margin-left: 13px;
                width: 251px;
                height: 112px;
                border-left: 1px solid #e0e0e0;
                text-align: center;
                color: #616161;
                .title {
                    font-size: 22px;
                    line-height: 1;
                    color: @baseColor;
                    margin: 0 0 5px;
                }
                .fee,
                .time {
                    font-size: 12px;
                    margin: 0 0 5px;
                }
                .fee {
                    margin: 0 0 16px;
                }
                a {
                    display: block;
                    border: 1px solid @baseColor;
                    width: 118px;
                    height: 28px;
                    font-size: 12px;
                    line-height: 28px;
                    background: #fff;
                    margin: 0 auto;
                    color: @baseColor;
                    transition: all 0.3s ease;
                    &:hover {
                        background: @baseColor;
                        color: #fff;
                    }
                }
            }
        }
    }
}
</style>



