          imgUrl: 'http://c1.mifile.cn/f/i/15/goods/sidebar/cunchu.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        }
      ],
      bags: [{
          sourceUrl: '//list.mi.com/23',
          imgUrl: 'http://c1.mifile.cn/f/i/g/2015/cn-index/xiangbao-80.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        },
        {
          sourceUrl: '//search.mi.com/search_%E6%97%85%E8%A1%8C%E7%AE%B1',
          imgUrl: 'http://c1.mifile.cn/f/i/15/goods/sidebar/lvxingxiang.jpg?width=40&height=40',
          name: '90',
          buyStatus: false
        },
        {
          sourceUrl: '//list.mi.com/22',
          imgUrl: 'http://c1.mifile.cn/f/i/g/2015/cn-index/fuzhuang-80.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        }
      ],
      rabbit: [{
          sourceUrl: 'http://mitu.mi.com/',
          imgUrl: 'http://c1.mifile.cn/f/i/g/2015/cn-index/mitu-80.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        },
        {
          sourceUrl: '//list.mi.com/59',
          imgUrl: 'http://c1.mifile.cn/f/i/g/2015/cn-index/shubiaodian-80.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        },
        {
          sourceUrl: '//list.mi.com/24',
          imgUrl: 'http://c1.mifile.cn/f/i/15/goods/sidebar/zhoubian1.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        },
        {
          sourceUrl: '//www.mi.com/zazhi/index.html',
          imgUrl: 'http://c1.mifile.cn/f/i/g/2015/cn-index/zazhi-80-80.jpg?width=40&height=40',
          name: '',
          buyStatus: false
        }
      ]
    }
  },
  methods: {
    evtSideEnter: function(menuType) {
      this.goodsStatus = true
      this.currGoods = this[menuType];
    },
    evtSideLeave: function() {
      this.goodsStatus = false
    }
  },
  computed: {
    // 
    filterCurrGoods: function() {
      // filerGoods535
      let filterGoods = [
        [],
        [],
        [],
        [],
        []
      ]
      // forEachES6
      // 
      // 
      // var users = [
      //  {
      //    firstname:'li',
      //    lastname:'lei'
      //  },
      //  {
      //    firstname:'han',
      //    lastname:'meimei'
      //  }
      //
      // ]
      // users.forEach(function (item,index,array) {
      //  item.fullname = item.firstname+item.lastname
      //
      // })
      this.currGoods.forEach(function(item, index) {
        let goodsIndex = Math.floor(index / 6)
        filterGoods[goodsIndex].push(item)
      })
      return filterGoods;
    }
  }
}
</script>
<style lang="less" scoped>
@baseColor: #ff6700;
.top-banner {
    position: relative;
    width: 1226px;
    margin: 0 auto;
    height: auto;
}
.site-category {
    position: absolute;
    left: 0;
    top: 0;
    width: 235px;
    padding: 20px 0;
    background: rgba(0, 0, 0, 0.6);
    height: auto;
    z-index: 11;
}
.side-left {
    margin: 0;
    padding: 0;
    width: 235px;
    height: 420px;
}
.side-item:hover {
    background: @baseColor;
}
.side-item-link {
    display: block;
    height: 42px;
    line-height: 42px;
    font-size: 14px;
    color: #fff;
    position: relative;
    padding-left: 30px;
}
.iconfont {
    position: absolute;
    right: 20px;
    top: 10px;
    color: rgba(255,255,255,0.5);
    font-size: 16px;
}
.site-category-detail {
    position: absolute;
    left: 235px;
    top: 0;
    box-shadow: 3px 8px 16px rgba(0,0,0,0.18);
    border: 1px solid #e0e0e0;
    background: #fff;
    width: auto;
    display: flex;
    flex-flow: row nowrap;
    z-index: 11;
}
.category-items {
    width: auto;
    height: 460px;
    margin: 0;
    padding: 0;
    list-style: none;
    background: #fff;
}
.category-goods {
    width: 265px;
    height: 76.6px;
    padding: 0 20px;
    box-sizing: border-box;
    background: #fff;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: row nowrap;
}
.goods-link {
    width: 170px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: row nowrap;
}
.text-name {
    padding-left: 10px;
    font-size: 14px;
}
.goods-buy-link {
    display: block;
    width: 58px;
    height: 22px;
    line-height: 24px;
    font-size: 12px;
    text-align: center;
    color: @baseColor;
    border: 1px solid @baseColor;
    &:hover {
        background: @baseColor;
        color: #fff;
    }
}
.fade-enter,
.fade-leave-to {
    opacity: 0;
    width: 0;
}
.fade-enter-active,
.fade-leave-active {
    transition: all 0.3s ease;
}
</style>



