<style lang="less" scoped>
@color: #ff6700;
#top-header {
    width: 100%;
    height: 100px;
    position: relative;
}
.container {
    position: relative;
    width: 1226px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.icon-mi {
    display: inline-block;
    width: 55px;
    height: 55px;
    background: url('../assets/icon-mi.png') no-repeat;
    background-size: 100% 100%;
}
.pic-gif {
    display: inline-block;
    width: 163px;
    height: 66px;
    background: url('../assets/win.gif') no-repeat;
    background-size: 100% 100%;
    margin-left: 10px;
}
.navs {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.nav-item {
    padding: 26px 10px 38px;
    text-decoration: none;
    color: #333;
    font-size: 16px;
    &:hover {
        color: @color;
    }
}
.header-search {
    width: 296px;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
}
.search-form {
    width: 296px;
    height: 50px;
    margin-top: 26px;
    position: relative;
    .search-text {
        position: absolute;
        left: 0;
        top: 0;
        width: 223px;
        height: 48px;
        border: 1px solid #e0e0e0;
        outline: 0;
        font-size: 14px;
        padding: 0 10px;
        line-height: 48px;
        transition: all 0.3s ease;
    }
    .search-btn {
        position: absolute;
        width: 51px;
        height: 48px;
        border: 1px solid #e0e0e0;
        outline: 0;
        right: 0;
        top: 0;
        padding: 0;
        border-left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: all 0.5s ease;
        .search-icon {
            display: block;
            width: 20px;
            height: 20px;
            background: url('../assets/icon-search.png') no-repeat;
            background-size: 100% 100%;
            transition: all 0.5s ease;
        }
        &:hover {
            background: @color;
            .search-icon {
                background: url('../assets/icon-search-white.png') no-repeat;
                background-size: 100% 100%;
            }
        }
    }
    // lesshover
    &:hover {
        .search-btn,
        .search-text {
            border-color: #b0b0b0;
        }
    }
}
.search-hot-words {
    position: absolute;
    top: 16px;
    right: 75px;
    z-index: 2;
    .words {
        padding: 2px;
        background: #eee;
        color: #757575;
        font-size: 12px;
        text-decoration: none;
        transition: all 0.5s ease;
        &:hover {
            color: #fff;
            background: @color;
        }
    }
}
.keyword-list {
    position: absolute;
    width: 243px;
    left: 0;
    top: 50px;
    border: 1px solid @color;
    border-top: 0;
    font-size: 10px;
    z-index: 12;
    a {
        text-decoration: none;
        display: block;
        color: #424242;
        background: #fff;
        &:hover {
            background: #fafafa;
        }
    }
    .list-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 8px;
        .item-price {
            color: #b0b0b0;
        }
    }
}
//  input border
.active {
    border-color: @color !important;
}
.header-menu {
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    background: #fff;
    box-shadow: 0 1px 5px #ccc;
    z-index: 12;
}
.menus {
    list-style: none;
    margin: 0;
    padding: 30px 0 30px 10px;
}
.menus-item {
    float: left;
}
.menus-item-pic-link {
    display: block;
    padding: 0 30px;
    border-right: 1px solid #ccc;
}
.menus-item-pic {
    width: 100%;
    width: 160px;
    height: 110px;
}
.menus-item-title {
    width: 100%;
    height: 16px;
    margin-top: 16px;
    font-size: 12px;
    text-align: center;
    .menus-item-title-link {
        text-decoration: none;
        color: #000;
    }
}
.menus-item-price {
    width: 100%;
    height: 14px;
    margin-top: 10px;
    font-size: 10px;
    text-align: center;
    color: @color;
}
.menus-item:last-child .menus-item-pic-link {
    border-right: 0;
}
// 
.fade-enter,
.fade-leave-to {
    opacity: 0;
    height: 0;
}
.fade-enter-to,
.fade-leave {
    height: 230px;
}
.fade-enter-active,
.fade-leave-active {
    transition: all 0.3s ease;
}
</style>



